/*******************************************************************************
 * Молния
 * Lightning
 */

export default (canvas)=>{
  const ctx = canvas.getContext('2d')
  let canvasWidth  = canvas.width = canvas.offsetWidth
  let canvasHeight = canvas.height = canvas.offsetHeight
  let circles      = []
  let circlesCount = 6
  let mouseX       = 0
  let mouseY       = 0
  let toggle       = 0
  const canvasColor  = '#000'
  const lightningMaxLength  = 800
  const lightningMaxScatter = 6
  const stepLength          = 2

  window.onresize = function(){
    canvasWidth = canvas.width=canvas.offsetWidth
    canvasHeight = canvas.height =canvas.offsetHeight
  }
  window.onkeydown=()=>{
    toggle == circles.length-1 ? toggle=0 : toggle++
  }
  canvas.onmousemove = e =>{
    // вычисление положене мыши на canvas:
    mouseX = e.x - canvas.getBoundingClientRect().x
    mouseY = e.y - canvas.getBoundingClientRect().y
  }

  class Circle{
    constructor(x, y){
      this.x = x || Math.random() * canvasWidth
      this.y = y || Math.random() * canvasHeight
    }
    draw(x, y){
      this.x = x || this.x
      this.y = y || this.y

      ctx.beginPath() // открытие пути рисования
      ctx.arc(this.x, this.y, 10, 0, Math.PI*2)
      ctx.closePath() // закрыть путь
      ctx.fillStyle   = `#FFF` // цвет точки
      ctx.fill()

      ctx.beginPath() // открытие пути рисования
      ctx.arc(this.x, this.y,26, 0, Math.PI*2)
      ctx.closePath() // закрыть путь
      ctx.strokeStyle = `#FFF` // цвет контура
      ctx.lineWidth   = 3    // ширина линии
      ctx.stroke()
    }
  }

  function createLightning(){
    for(let a=0; a<circles.length; a++){
      for(let b=a+1; b<circles.length; b++){
        let dist        = getDistance(circles[a], circles[b])
        let chance      = dist / lightningMaxLength
        if(chance > Math.random()) continue
        // изменения цвета при приближении
        let otherColor = chance * 255
        // дробление линии
        let stepsCount = dist / stepLength
        let stepX  = circles[a].x
        let stepY  = circles[a].y
        ctx.lineWidth   = 2.5
        ctx.strokeStyle = `rgb(185, ${otherColor}, ${otherColor}`
        ctx.beginPath()
        ctx.moveTo(circles[a].x, circles[a].y)
        // дробление линии и отрисовка
        for(let j=stepsCount; j>1;j--){
          // смещение молнии в центре
          let pathLength = getDistance(circles[a], {x:stepX, y:stepY})
          let offset     = Math.sin(pathLength / dist * Math.PI) * lightningMaxScatter

          stepX += (circles[b].x - stepX) / j + Math.random() * offset * 2 - offset
          stepY += (circles[b].y - stepY) / j + Math.random() * offset * 2 - offset

          ctx.lineTo(stepX, stepY)
        }
        ctx.stroke()
      }
    }
  }

  function getDistance(a, b){
    return  Math.sqrt(Math.pow(b.x-a.x, 2) + Math.pow(b.y-a.y, 2))
  }

  function reDrawBackground(){
    ctx.fillStyle = canvasColor
    ctx.fillRect(0,0, canvasWidth, canvasHeight)
  }

  function init(){
    for(let i=0; i<circlesCount; i++){
      circles.push(new Circle())
    }
  }

  function loop(){
    ctx.clearRect(0,0, canvasWidth, canvasHeight) // очистка canvas

    reDrawBackground()
    createLightning()
    circles.map( i =>{
      i == circles[toggle] ? i.draw(mouseX, mouseY) : i.draw()
    })

    return canvas.offsetWidth < 500 ? null : requestAnimationFrame(loop)
  }

  init()
  loop()
}
