import React from 'react'
import { Link } from 'react-router-dom'
import './button.scss'

export default ({children, active, to, link, btn, click})=>{
  return(
    <>
    { !!link ? <a href={link} className={`button button_${active ? 'active' : 'none'}`}>{children}</a> : null }
    { !!to ? <Link to={to} className={`button button_${active ? 'active' : 'none'}`}>{children}</Link> : null}
    { !!btn? <button onClick={click} className={`button button_${active ? 'active' : 'none'}`}>{children}</button> : null}
    </>
  )
}
