import React from 'react'
import styles from './tools.module.scss'

export default ({dataset})=>{
  const list = dataset.map((data, id)=>
    <a href={data.url}
       key={id}
       target="_blank"
       className={styles.link}
      >{data.text} &nbsp;
    </a>
  )
  return <div className={styles.list}>{list}</div>
}
