import React, { useState, useEffect } from "react"
import Button from '../../components/Button'
import Canvas from '../../components/Canvas'
import {cube} from '../../canvases'
import styles from './home.module.scss'
import classNames from 'classnames'

let signs=
  `Здравствуйте, я начинающий web-программист. Данная "spa" страница
   была создана мною для того, чтобы вы смогли правильно оценить
   мои навыки работы с кодом, до официального приглашения
   на работу`.split('')

export default ()=>{
  let [count, setCount] = useState(0)
  let [text, setText] = useState('')

  useEffect(()=>{
    const interval = setInterval(()=>{
      if(signs.length == count) return
      setText(text + signs[count]) // вставка текста
      setCount(count+=1)
    }, 8)

    return ()=>clearInterval(interval)
  }, [count])

  return(
    <div className={styles.wrap}>
      <Canvas method={cube} isDecoration />
      <div className={styles.buttons}>
        <Button link='https://github.com/tealikes'>GitHub</Button>
        <Button to='summary'>Моя анкета</Button>
        <Button to='jobs'>Мои работы</Button>
      </div>
      <div className={styles.text}>
        {text}
      </div>
    </div>
  )
}
