import React, {useEffect, useState} from 'react'
import s from './canvas.module.scss'

export default ({method, isDecoration})=>{
  let mount, cls

  const [isBig, setBig] = useState(false)

  function handler(){
    setBig(!isBig)
  }

  useEffect(()=>{
    method(mount)
  }, [isBig] )

  return isDecoration ?
    <canvas className={ s.decoration } ref={ el=> mount=el } /> :
    <canvas className={ isBig ? s.big : s.mini } onClick={handler} ref={ el=> mount=el } />
}
